.nav__btn {
    position: relative;
    padding: 5px 30px;
    border-radius: 20px;
    border: 2px solid #ffb108;
    background: linear-gradient(to bottom, transparent 50%, #ffb108 50%);
    background-size: 100% 200%;
    background-position: left 0%;
    transition: all 200ms ease-in-out;
}

.nav__btn:before {
	content:'';
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	height: 0;
	background: rgba(255,255,255,0.3);
	border-radius: 20px;
	transition: all 0.3s ease-in;

}

.nav__btn:hover:before {
	height: 100%;
    top: 0%;
}
